
@import 'variables.scss';


nav{
  z-index: 9;
}

header {
  grid-template: "header";
  position: relative;
  background-image: url('/images/background.jpg');
  min-height: 100vh;
  min-width: 100%;
  background-size: cover;
  background-position: center bottom;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  z-index: 1;

  .dark-overlay {
    position:absolute;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.70);
  }

  .title-container {
    color: $off-white;
    z-index: 1;
    margin: 0 auto;

    h1{
      margin: 0;
      border: 2px solid $off-white;
      border-radius: 2px;
      display: inline-block;
      overflow: hidden;
      padding-right: 1rem;

      .intro-span{
        display: inline-block;
        background: $off-white ;
        color: $off-black;
        padding: 1rem;
        margin-right: 1rem;
      }
    }
    h2 {
      a {
        color: $off-black;
        transition: 0.4s all;
        i {
          transition: 0.4s all;
          color: $off-white;
        }
        &:hover {
          i {
            transform: scale(1.5) translateY(1rem);
            color: $off-white;
          }
        }
      }
    }
  }
}

.about {
  padding: 1.8rem 20%;
  display: flex;
  flex-wrap: wrap;

  h3 {
    width: 100%;
    text-align: center;
  }

  img {
    float: left;
    padding: 2rem;
    border-radius: 50%;
    max-width: 250px;
  }
}


.projects {
  display: flex;
  flex-direction: row;
  flex-flow: wrap;

  .project-container {
    position: relative;
    transition: 0.4s all;
    height: calc(25vw);
    width: calc(100% / 3);
    display: flex;
    flex-direction: row;
    align-items: center;
    &:hover {
      z-index: 999;
      transform: scale(1.2);
      box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
      &:nth-child(3n -2 ) {
        transform: translateX(10%) scale(1.2);
      }
      &:nth-child(3n) {
        transform: translateX(-10%) scale(1.2);
      }
    }
    img {
      margin: 0 auto;
      max-width: 80%;
      max-height: 80%;
      transition: 0.4s ease all;
    }
  }
}
